html {
  font-size: 18px;
}

body {
  font-family: var(--ff-body);
  font-weight: var(--fw-400);
  font-size: 1rem;
  line-height: 1.4;
  font-style: normal;
  color: var(--col-dark);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.ff-body {
  font-family: var(--ff-body);
}

h1,
.h1,
h2,
.h2,
h3,
.h3 {
  font-family: var(--ff-heading);
  font-weight: var(--fw-800);
  line-height: 1.1;
  text-wrap: balance;
  margin-bottom: 0.5rem;
}

h1,
.h1 {
  font-size: var(--fs-900);
}

h2,
.h2 {
  font-size: var(--fs-800);
}

h3,
.h3 {
  font-size: var(--fs-700);
}

h4,
.h4 {
  font-size: var(--fs-600);
}

h5,
.h5 {
  font-size: var(--fs-500);
}

.has-large-font-size,
.fs-600 {
  font-size: var(--fs-600);
}

.fs-500 {
  font-size: var(--fs-500);
}

.fs-400 {
  font-size: var(--fs-400);
}

.fs-300 {
  font-size: var(--fs-300);
}

.fw-400 {
  font-weight: var(--fw-400);
}

strong,
.fw-bold,
.fw-600 {
  font-weight: var(--fw-600);
}

.text-balance {
  text-wrap: balance;
}

.w-constrained {
  max-width: 90ch;
  margin-inline: auto;
}

/* Colour Classes */
.has-dark-color,
.text--dark {
  color: var(--col-dark);
}

.has-dark-background-color,
.bg--dark {
  background-color: var(--col-dark);
}

.has-light-color,
.text--light {
  color: var(--col-light);
}

.has-light-background-color,
.bg--light {
  background-color: var(--col-light);
}

.has-gradient-background {
  background: var(--col-gold-gradient);
}

.has-blue-400-color,
.text--blue-400 {
  color: var(--col-blue-400);
}

.button {
  position: relative;
  display: inline-block;
  padding: 0.5rem 2rem;
  font-size: var(--fs-400);
  font-weight: var(--fw-600);
  color: var(--col-dark);
  background: transparent;
  border: 2px solid var(--col-dark);
  border-radius: 100vh;
  cursor: pointer;
  transition: color 0.3s, border-color 0.3s, background 0.3s, box-shadow 0.3s;
  z-index: 1;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.button:hover {
  text-decoration: none;
}

.button::before {
  content: "";
  position: absolute;
  inset: -2px; /* Matches border thickness */
  border-radius: inherit;
  background: linear-gradient(45deg, #c68066, #e6b8a2, #f4cccc, #c68066);
  background-size: 300% 300%;
  animation: animatedgradient 3s infinite linear;
  z-index: -1;
  opacity: 0;
  transition: opacity 300ms ease-in-out; /* Smooth fade in/out */
  mask: linear-gradient(white 0 0) content-box, linear-gradient(white 0 0);
  -webkit-mask: linear-gradient(white 0 0) content-box, linear-gradient(white 0 0);
  mask-composite: exclude;
  -webkit-mask-composite: xor;
  padding: 2px; /* Matches the border thickness */
}

.button:hover,
.button:focus,
.button:focus-visible {
  color: #c68066;
  background-color: #faf6f6;
  border-color: transparent; /* Hide the solid border */
  box-shadow: 0 0 20px rgba(198, 128, 102, 0.6); /* Glow effect */
}

.button:focus::before,
.button:focus-visible::before,
.button:hover::before {
  opacity: 1;
}

.button:not(:hover) {
  box-shadow: 0 0 10px rgba(198, 128, 102, 0); /* Fades glow out smoothly */
}

@keyframes animatedgradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
