.quote_cta__quote {
  font-family: var(--ff-heading);
  font-size: var(--fs-700);
  font-style: italic;
}

.countup {
  background: var(--col-gold-gradient);
}
.countup .counter {
  font-family: var(--ff-heading);
  font-size: var(--fs-900);
}

.large_text__cta__card {
  border: 2px solid var(--col-accent-400);
  background: var(--col-gold-gradient);
  text-align: center;
  padding: 1rem;
}
.large_text__cta__title {
  font-family: var(--ff-heading);
  font-size: var(--fs-700);
  line-height: 1.2;
  margin-bottom: 1rem;
}

.three_image_cta__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  isolation: isolate;
}
.three_image_cta__image img {
  width: 100%;
  height: auto;
}
.three_image_cta__image:nth-of-type(1) {
  margin-right: -2rem;
  z-index: 3;
}
.three_image_cta__image:nth-of-type(2) {
  margin-inline: -2rem;
  -webkit-margin-before: 2rem;
  margin-block-start: 2rem;
  z-index: 2;
}
.three_image_cta__image:nth-of-type(3) {
  margin-left: -2rem;
  z-index: 1;
}

.portfolio__grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}
.portfolio__item {
  position: relative;
  overflow: hidden;
}
.portfolio__item img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  display: block;
}
@media (min-width: 768px) {
  .portfolio__grid {
    grid-auto-rows: 300px;
    grid-auto-flow: dense;
    grid-template-columns: repeat(2, 1fr);
  }
  .portfolio__item:nth-child(4n+1) {
    grid-column: 1;
    grid-row: span 1;
  }
  .portfolio__item:nth-child(4n+2) {
    grid-column: 2;
    grid-row-start: auto;
    grid-row-end: span 2;
  }
  .portfolio__item:nth-child(4n+3) {
    grid-column: 1;
    grid-row-start: auto;
    grid-row-end: span 2;
  }
  .portfolio__item:nth-child(4n+4) {
    grid-column: 2;
    grid-row: span 1;
  }
}
@media (min-width: 992px) {
  .portfolio__grid {
    grid-template-columns: repeat(3, 1fr);
  }
  .portfolio__item:nth-child(6n+1) {
    grid-column: 1;
    grid-row: span 1;
  }
  .portfolio__item:nth-child(6n+2) {
    grid-column: 2;
    grid-row: span 2;
  }
  .portfolio__item:nth-child(6n+3) {
    grid-column: 3;
    grid-row: span 1;
  }
  .portfolio__item:nth-child(6n+4) {
    grid-column: 1;
    grid-row: span 2;
  }
  .portfolio__item:nth-child(6n+5) {
    grid-column: 2;
    grid-row: span 1;
  }
  .portfolio__item:nth-child(6n+6) {
    grid-column: 3;
    grid-row: span 2;
  }
}
.portfolio .glightbox {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
@media (min-width: 768px) {
  .portfolio .glightbox:hover .portfolio__caption {
    transform: translateY(0);
  }
}
.portfolio .glightbox .portfolio__caption {
